  color: $default-text-color !important;
  background-color: $form-control-bg !important;
  font-size: 0.875rem !important;
  font-weight: $default-font-weight !important;
  line-height: 1.5 !important;
  border-radius: 0.35rem !important;
  padding: 0.5rem 0.85rem !important;
  background-image: none !important;
  height: inherit !important;

  &:focus {
    border-color: $primary-05 !important;
    box-shadow: 0 0 4px $primary-05 !important;
    
    &::placeholder {
      padding: 0 !important;
    }
  }

  &::placeholder {
    font-size: 0.8rem !important;
    font-weight: 400;
    opacity: 0.6;
    color: $default-text-color !important;

    &:focus {
      padding: 0 !important;
      font-size: 0.8rem !important;
    }
  }
}

.autoComplete_wrapper>ul {
  box-shadow: $box-shadow !important;
  background-color: $custom-white !important;
  border: 1px solid $default-border !important;
  margin-top: 0 !important;

  .no_result {
    padding: 0.5rem !important;
  }


  >li {
    font-size: 0.85rem !important;
    background-color: $custom-white !important;
    color: $default-text-color !important;

    &:hover {
      background-color: $primary-01 !important;
      color: $default-text-color !important;
    }

    mark {
      background-color: transparent;
      color: $orange !important;
      font-weight: bold;
      padding: 0 !important;
    }
  }
}

#autoComplete_list_2 {
  p {
    padding: 0.5rem !important;
    margin-block-end: 0 !important;
  }
}
.autoComplete_wrapper > input {
    width: auto !important;
}
/* End:: Auto Complete */

