/* Start:: Tagify JS */
.tagify { 
  --tag-inset-shadow-size: 0 !important;
}
.tagify__tag {
  background-color: $light;
}
.tagify.tagify--mix.form-control {
  padding: 0.5rem;
}
.tagify__tag>div::before {
  box-shadow: 0 0 0 var(--tag-inset-shadow-size) var(--gray-3) inset !important;
}
.tagify:hover:not(.tagify--focus):not(.tagify--invalid) {
  border-color: $input-border !important;
}

.tagify {
  border-color: $input-border !important;

  &.tagify--focus {
    border-color: var(--primary05) !important;
    box-shadow: 0 0 4px var(--primary05) !important;
  }
}
.tagify__tag {
  margin-inline: 7px 0 !important;
  margin-block: 7px !important;
}
.tagify__tag>div {
  font-size: 13px;
}

.tagify__input {
  margin: 3px !important;
  line-height: 1.85rem !important;
}

.tagify {
  --tags-disabled-bg: var(--gray-3) !important;
  --tag-bg: var(--gray-3) !important;
  --tag-hover: rgba(var(--primary-rgb), 0.15) !important;
  --tag-text-color: var(--default-text-color) !important;
  --tag-text-color--edit: var(--default-text-color) !important;
  --tag-invalid-color: rgba(var(--danger-rgb), 0.5) !important;
  --tag-invalid-bg: rgba(var(--danger-rgb), 0.2) !important;
  --tag-remove-bg: rgba(var(--danger-rgb), 0.1) !important;
  --tag-remove-btn-color: var(--default-text-color) !important;
  --tag-remove-btn-bg--hover: rgba(var(--danger-rgb), 0.3) !important;
  --tag-pad: 0.2em 0.5em !important;
}

.tagify__tag__removeBtn {
  margin: 0 0.25rem !important;
}
.tagify__tag:focus div::before, .tagify__tag:hover:not([readonly]) div::before {
  --tag-bg-inset: 0px !important;
}
.tagify__tag-text {
  padding: 3px;
}

.tags-look {
  border: 1px solid $default-border !important;
  background-color: $custom-white !important;

  .tagify__dropdown__item {
    display: inline-block;
    vertical-align: middle;
    border-radius: 3px;
    padding: .3em .5em;
    border: 1px solid $default-border;
    background: $custom-white;
    margin: .2em;
    font-size: .85em;
    color: $default-text-color;
    transition: 0s;
  }
}

.tags-look .tagify__dropdown__item--active {
  color: $default-text-color;
}

.tags-look .tagify__dropdown__item:hover {
  background: $primary;
  border-color: $primary;
  color: $white;
}

.tags-look .tagify__dropdown__item--hidden {
  max-width: 0;
  max-height: initial;
  padding: .3em 0;
  margin: .2em 0;
  white-space: nowrap;
  text-indent: -20px;
  border: 0;
}

.tagify__dropdown {
  border-top: 1px solid $primary !important;
}

/* Suggestions items */
.tagify__tag .tagify__tag__avatar-wrap {
  width: 22px;
  height: 22px;
  white-space: normal;
  border-radius: 50%;
  margin-right: 5px;
  transition: .12s ease-out;
  vertical-align: middle;
}

.tagify__tag img {
  width: 100%;
  vertical-align: top;
}

.tagify__dropdown.users-list .tagify__dropdown__item {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 0 1em;
  grid-template-areas: "avatar name"
    "avatar email";
}

.tagify__dropdown.users-list header.tagify__dropdown__item {
  grid-template-areas: "add remove-tags"
    "remaning .";
}

.tagify__dropdown.users-list .tagify__dropdown__item:hover .tagify__dropdown__item__avatar-wrap {
  transform: scale(1.2);
}

.tagify__dropdown.users-list .tagify__dropdown__item__avatar-wrap {
  grid-area: avatar;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  overflow: hidden;
  background: $light;
  transition: .1s ease-out;
}

.tagify__dropdown.users-list img {
  width: 100%;
  vertical-align: top;
}

.tagify__dropdown.users-list header.tagify__dropdown__item>div,
.tagify__dropdown.users-list .tagify__dropdown__item strong {
  grid-area: name;
  width: 100%;
  align-self: center;
}

.tagify__dropdown.users-list span {
  grid-area: email;
  width: 100%;
  font-size: .9em;
  opacity: .6;
}

.tagify__dropdown.users-list .tagify__dropdown__item__addAll {
  border-bottom: 1px solid $default-border;
  gap: 0;
}

.tagify__dropdown.users-list .remove-all-tags {
  grid-area: remove-tags;
  justify-self: self-end;
  font-size: .8em;
  padding: .2em .3em;
  border-radius: 3px;
  user-select: none;
}

.tagify__dropdown.users-list .remove-all-tags:hover {
  color: $white;
  background: $orange;
}


/* Tags items */
#users-list .tagify__tag {
  white-space: nowrap;
}

#users-list .tagify__tag img {
  width: 100%;
  vertical-align: top;
  pointer-events: none;
}


#users-list .tagify__tag:hover .tagify__tag__avatar-wrap {
  transform: scale(1.6) translateX(-10%);
}

#users-list .tagify__tag .tagify__tag__avatar-wrap {
  width: 16px;
  height: 16px;
  white-space: normal;
  border-radius: 50%;
  background: $light;
  margin-right: 5px;
  transition: .12s ease-out;
}

.users-list .tagify__dropdown__itemsGroup:empty {
  display: none;
}

.users-list .tagify__dropdown__itemsGroup::before {
  content: attr(data-title);
  display: inline-block;
  font-size: .9em;
  padding: 4px 6px;
  margin: var(--tagify-dd-item-pad);
  font-style: italic;
  border-radius: 4px;
  background: $success;
  color: $custom-white;
  font-weight: 600;
}

.users-list .tagify__dropdown__itemsGroup:not(:first-of-type) {
  border-top: 1px solid $default-border;
}

.tagify__dropdown__wrapper {
  border: 0 !important;
  box-shadow: none !important;
  background-color: $custom-white !important;
}

.tagify__dropdown {
  padding: 0.25rem !important;
  border-bottom-width: 0;
  background: $custom-white !important;
  border: 1px solid $default-border !important;
  box-shadow: $box-shadow !important;
  border-radius: $default-radius !important;
}

.tagify__dropdown__item--active {
  background: $primary !important;
  color: $white !important;
}

.remove-all-tags {
  color: $white;
}
.tagify--mix .tagify__input {
  padding: 0 !important;
  margin: 0 !important;
}
.tagify__input::before {
  color: $default-text-color !important;
}
/* End:: Tagify JS */

/* Start:: Dual List Box */
.dual-listbox .dual-listbox__search {
  border-color: var(--input-border) !important;
  color: var(--default-text-color);
  background-color: var(--form-control-bg);
  font-size: 0.875rem;
  font-weight: var(--default-font-weight);
  line-height: 1.5;
  border-radius: 0.35rem;
  padding: 0.5rem 0.85rem;
  outline: none;
  &::-webkit-input-placeholder { 
    color: $text-muted !important;
  }
}
.dual-listbox {
  .dragging {
    background: $light !important;
  }
  .drop-above {
    border-top: 1px solid $default-border !important;
  }
} 
.dual-listbox .dual-listbox__title {
  border-left: 1px solid $default-border !important;
  border-right: 1px solid $default-border !important;
  border-top: 1px solid $default-border !important;
  font-weight: 500 !important;
}
.dual-listbox .dual-listbox__available, .dual-listbox .dual-listbox__selected {
  border: 1px solid $default-border !important;
}
.dual-listbox .dual-listbox__item {
  border-bottom: 1px solid $default-border !important;
}
.dual-listbox .dual-listbox__button {
  background-color: $primary !important;
  border-radius: $default-radius;
}
.dual-listbox .dual-listbox__item.dual-listbox__item--selected {
  background-color: $primary-01 !important;
}
.dual-listbox .dual-listbox__available, .dual-listbox .dual-listbox__selected {
  width: auto !important;
}
/* End:: Dual List Box */

/* Start:: Toastify */
.toastify-right {
  inset-inline-end: 15px !important;
}
.toast-close {
  padding: 0 5px 0 15px !important;
}
[dir="rtl"] {
  .toast-close {
    padding: 0 15px 0 5px !important;
  }
  .toastify-right {
    right: inherit !important;
  }
}
/* End:: Toastify */