/* Start::button default */
.btn {
  font-size: .85rem;
  border-radius: .25rem;
  padding: .375rem .75rem;
  box-shadow: none;
  font-weight: 500;

  &.btn-sm {
    padding: .26rem .5rem;
    border-radius: .25rem;
    font-size: .8rem;
  }

  &.btn-lg {
    padding: .65rem 1rem;
    border-radius: $default-radius;
    font-size: .95rem;
  }
}

.btn-group {
  .btn {
    padding: .45rem .75rem;
  }
}

.btn-group-lg {
  .btn {
    padding: .65rem 1rem;
  }
}

.btn-group-sm {
  .btn {
    padding: .25rem .5rem;
  }
}

.btn-check:active+.btn,
.btn-check:checked+.btn,
.btn.active,
.btn.show,
.btn:active {
  color: $white;
}

.btn-check:focus+.btn,
.btn:focus {
  box-shadow: none;
}

/* End::button default */

/* Start::button colors */
@mixin btn-primary($btn-color) {
  background-color: rgb(#{$btn-color}) !important;
  border-color: rgb(#{$btn-color}) !important;
  color: $white !important;

  &:hover {
    background-color: rgba(#{$btn-color}, 0.9) !important;
    border-color: rgb(#{$btn-color}) !important;
    color: $white;
  }

  &:focus {
    background-color: rgb(#{$btn-color});
    border-color: rgb(#{$btn-color});
    color: $white;
  }
}

@mixin btn-check-focus($btn-color) {
  background-color: rgb(#{$btn-color});
  border-color: rgb(#{$btn-color});
  outline: 0;
  box-shadow: none;
}

/* btn-primary starts */
.btn-primary {
  @include btn-primary(var(--primary-rgb));
}

.btn-check:focus+.btn-primary,
.btn-primary:focus {
  @include btn-check-focus(var(--primary-rgb));
}

.btn-check:active+.btn-primary:focus,
.btn-check:checked+.btn-primary:focus,
