  .custom-btn-icons {
    box-shadow: 0px 0px 1px .25rem rgba(0, 0, 0, 0.1);
    position: absolute;
    inset-inline-start: -0.125rem;
    inset-block-start: 0px;
    background-color: $custom-white;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    padding: .375rem;
    border-radius: 3.125rem;
    font-size: 1rem;
    width: 2rem;
    height: 2rem;

    i {
      position: absolute;
    }
  }
